<template>
  <div>
    <router-link class="uk-link-muted" to="home">„Éõ„Éº„É†„Å´Êàª„Çã</router-link>
    <div class="uk-width-1-2 uk-margin-auto uk-margin-medium">
      <vk-card v-on:click="createRoom()" type="primary">
        <vk-card-title>ÂØæÊà¶„É´„Éº„É†‰ΩúÊàê</vk-card-title>
        <p>Êñ∞„Åó„ÅèÂØæÊà¶ÈÉ®Â±ã„Çí‰Ωú„Çä„Åæ„Åô</p>
      </vk-card>
    </div>
    <div class="uk-width-1-2 uk-margin-auto uk-margin-medium">
      <vk-card v-on:click="joinRoom()" type="primary">
        <vk-card-title>ÂÖ•ÂÆ§</vk-card-title>
        <p>„É´„Éº„É†ID„ÇíÂÖ•Âäõ</p>
        <input class="uk-input uk-form-width-medium uk-form-large" type="number" v-model="enterCode" placeholder="1234">
      </vk-card>
    </div>
    <div v-if="isFailedCreated" class="uk-text-center@s">ÈÉ®Â±ã„ÅÆ‰ΩúÊàê„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÅÊôÇÈñì„ÇíÁΩÆ„ÅÑ„Å¶ÂÜçÂ∫¶„ÅäË©¶„Åó„Åè„Å†„Åï„ÅÑ</div>
  </div>
</template>

<script>
  export default {
    name: 'rooms',
    data () {
      return{
        enterCode: null,
        isFailedCreated: false
      }
    },
    methods: {
      createRoom () {
        const sendObj = {
          token: localStorage.getItem('token')
        }
        const method = 'POST'
        const body = Object.keys(sendObj).map((key)=>key+"="+encodeURIComponent(sendObj[key])).join("&")
        const headers = {
          'Accept': 'application/json',
          'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'
        }
        fetch('/api/rooms', { method, headers, body }).then((res) => res.json()).then (res => {
          if (res.status === 'ok') {
            this.$router.push(`/rooms/${res.id}/waiting`)
          } else {
            this.isFailedCreated = true
          }
        })
        return false
      },
      joinRoom () {
        const sendObj = {
          token: localStorage.getItem('token'),
          enterCode: this.enterCode
        }
        const method = 'POST'
        const body = Object.keys(sendObj).map((key)=>key+"="+encodeURIComponent(sendObj[key])).join("&")
        const headers = {
          'Accept': 'application/json',
          'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8'
        }
        fetch(`/api/rooms/${sendObj.enterCode}/join`, { method, headers, body }).then((res) => res.json()).then (res => {
          if (res.status === 'ok') {
            this.$router.push(`/rooms/${res.roomId}/waiting`)
          } else {
            //console.log('„Å∞„Åã')
          }
        }).catch(err => {
          //console.log('„ÅÇ„Åª')
        })
      }
    }
  }
</script>